<template>
	<div class="split-container">
        <div class="split left">
        	<div class="split-text-view">
	        	<h1 class="animated flash">奥塔奇教育</h1>
	            <p>内蒙古奥塔奇<strong>蒙医</strong>教育是全区<strong>唯一</strong>一家蒙医执业医师、执业助理医师资格考试培训机构。拥有全区最权威的师资团队。我们一切的努力只为您最后的考试通过。本教育以最优质的服务让您轻松学习➕提升自己➕拿到优异成绩。</p>
	            <a href="http://www.utaqi.com/#/stu" class="button">报名情况查询</a>
	        </div>
        </div>
        <div class="split right">
        	<div class="split-text-view">
        		<h1 class="animated flash">喜讯</h1>
	            <p>本教育邀请历年考官精心编制的第一套蒙医助理医师模拟考试已经出炉，本套习题分为第一、第二两大部分，第一部分包含蒙医基础理论、诊断、蒙药学、方剂、西医诊断、法律法规及传染病学；第二部分包括内科、外科、妇科、儿科、骨科、疗术及温病。每部分150道题，一共300道题。现免费提供给广大考生。</p>
	            <a href="http://www.utaqi.com/#/exam" class="button">获取模拟考题</a>
        	</div>
        </div>
    </div>
</template>

<script>
	export default{
		name:"split-view"
	}
</script>

<style>
	:root{
		--container-bg-color:#333;
	    --left-bg-color:rgba(0,0,0,0.5);
	    --right-bg-color:rgba(0,0,0,0.6);
	    --left-button-hover-bg-color:rgba(161,11,11,0.3);
	    --right-button-hover-bg-color:rgba(9,63,112,0.3);
	    --hover-width:75%;
	    --other-width:25%;
	    --speed:1000ms;
	}
	.split-container{
	    position: relative; 
	    width: 100%;
	    height: 500px;
	    background-color: var(--container-bg-color);
	}

	.split-text-view{
	    position: absolute;
	    width: 100%;
	    height: 100%;
	    overflow: hidden;
	}

	.split-text-view p{
		color: #fff;
		padding: 20px;
		margin-top:140px;
		font-size: 1.8rem;
		text-align: justify;
		line-height: 1.8;
	}

	.split{
	    position: absolute;
	    width: 50%;
	    height: 100%;
	    overflow: hidden;
	}

	.split.left{
	    left: 0;
	    background: url("./../assets/left.jpg") center center no-repeat;
	    background-size: cover;
	    box-sizing: border-box;
	}

	.split.left::before{
	    position: absolute;
	    content: "";
	    width: 100%;
	    height: 100%;
	    background: var(--left-bg-color);
	}

	.split.right{
	    right: 0;
	    background: url("./../assets/right.jpg") center center no-repeat;
	    background-size: cover;
	}

	.split.right::before{
	    position: absolute;
	    content: "";
	    width: 100%;
	    height: 100%;
	    background: var(--right-bg-color);
	}

	.split-text-view h1{
	    font-size: 4rem;
	    color: #fff;
	    position: absolute;
	    left: 50%; 
	    transform: translateX(-50%);
	    white-space: nowrap;
	    top:40px;
	}

	.button{
	    display: block;
	    box-sizing: border-box;
	    position: absolute;
	    left: 50%;
	    transform: translateX(-50%);
	    bottom:80px;
	    /*height: 2.5rem;*/
	    width: 15rem;
	    padding: 1rem;
	    text-align: center;
	    color: #fff;
	    border: #fff solid 0.1rem;
		border-radius: 0.4rem;
	    font-size: 1.5rem;
	    font-weight: bold;
	    text-decoration: none;
		background: rgba(0,0,0,0.5);
	}

	.split.left .button:hover{
	    background: var(--left-button-hover-bg-color);
	    border-color: var(--left-button-hover-bg-color);
	    color: #fff;
	}

	.split.right .button:hover{
	    background: var(--right-button-hover-bg-color);
	    border-color: var(--right-button-hover-bg-color);
	    color: #fff;
	}

	/* 过渡动画 */
	.split.left, .split.right, .split.right::before, .split.left::before{
	    transition: var(--speed) all ease-in-out;
	}

	/* hover */
	.hover-left .left{
	    width: var(--hover-width);
	}
	.hover-left .right{
	    width: var(--other-width);
	}
	.hover-left .right::before{
	    z-index: 2;
	}

	.hover-right .right{
	    width: var(--hover-width);
	}
	.hover-right .left{
	    width: var(--other-width);
	}
	.hover-right .left::before{
	    z-index: 2;
	}

	/* 响应式 */
	@media(max-width:800px){
	    h1{
	        font-size:4rem;
	    }
	    .button{
	        width:12rem;
		    padding: 0.8rem;	        
	    }
	    .split-text-view p{
	    	font-size: 1.5rem;
	    	line-height: 2;
	    }
	    .split-container{
		    height: 1000px;
		}
	    .split{
		    position: absolute;
		    width: 100%;
		    height: 100%;
		    overflow: hidden;
		}
		.split.left{
			height: 500px;
		}
		.split.right{
		    right: 0;
		    top: 500px;
		    height: 500px;
		    /*background: url("./../assets/right.jpg") center center no-repeat;
		    background-size: cover;*/
		}
		.button{
			bottom: 60px;
		}
	}
</style>


