    }
    *{
        box-sizing: border-box;
    }
    html,body{
    	margin:0;
    	padding:0;
    	width:100%;
    	height: 100%;
    	overflow:scroll;
	    overflow-y:auto;
	    overflow-x: hidden;
	    font-family: 'Segoe UI',Tahoma,Geneva,Verdana;
    }

	#app{
		margin:0;
		/*padding-right等于footer的高度*/
	    padding: 0 0 220px 0;
	    line-height: 1.5;
	    page-break-before: auto;
	    page-break-after: always;
	    min-height: 100%;
	    height: auto !important;
	    height:100%;
	    min-height: 100%;
	    position: relative;
	}
	.btn{
	    border: none;
	    border-radius: 4px;
	    padding: 5px 10px;
	    margin-top: 10px;
	    text-align: center;
	    -webkit-transition-duration: 0.4s; /* Safari */
	    transition-duration: 0.4s;
	    text-decoration: none;
	    overflow: hidden;
	    cursor: pointer;
	    outline: none;
    }
    .btn-primary{
		background-color: #2278cf;
		color: #FFFFFF;
    }
    article{
	    line-height: 2;
	    margin:0px;
	    font-size: 16px;
		}
    .content{
            padding: 0 20px;
    }
    .panel{
            border-radius: 6px;
    }
    .iconfont{
        font-family:"iconfont" !important;
        font-size:16px;font-style:normal;
        -webkit-font-smoothing: antialiased;
        -webkit-text-stroke-width: 0.2px;
        -moz-osx-font-smoothing: grayscale;
    }

	.gotop{
        position: fixed;
        bottom:5px;
        right: 10px;
    }
    .gotop ul{
        list-style-type: none;
    }
    .gotop li a{
        display: block;
        width: 50px;
        /* border-bottom: 1px solid rgba(0,0,0,0.9); */
        background-color: rgba(0,0,0,0.7);
        color: #fff;
        padding: 15px;
        text-align: center;
    }
  .gotop li a.user{border: 0;}
  .gotop li a i{line-height: 15px;font-size: 20px;}
  .gotop li a em{display: none;}
  .gotop li a:hover{
      background-color:rgba(255, 255, 255, 0.7);
      color: #333;
      border: 1px solid #efefef;
      text-decoration: none;
    }
  .gotop li a:hover em{display: block;font-style: normal;}
    
    @media (min-width: 1200px) {
		.gotop{
            position: fixed;
            bottom:22%;
            right: 100px;
        }
	}
</style>



