<template>
  <swiper :options="swiperOption" ref="mySwiper">
    <!-- slides -->
    <swiper-slide>
      <img src="https://utaqi-1253553465.cos.ap-beijing.myqcloud.com/baim-hanif-89800.jpg?sign=q-sign-algorithm%3Dsha1%26q-ak%3DAKIDh8GkTFSTmA72aUaXYc6nwZaRILkCDep5%26q-sign-time%3D1517809168%3B1517810968%26q-key-time%3D1517809168%3B1517810968%26q-header-list%3D%26q-url-param-list%3D%26q-signature%3D94173ae8048f0bb519002dd44bd072cbd6f9bec3&token=4a10302a853f8a93878d84f99819369d2af2685110001&clientIP=116.112.6.178&clientUA=538862bf-a6ae-432a-a850-1899a4cffb08">
    </swiper-slide>
    <swiper-slide>
      <img src="https://utaqi-1253553465.cos.ap-beijing.myqcloud.com/1.jpg?sign=q-sign-algorithm%3Dsha1%26q-ak%3DAKIDZ2edKZSW2unOHeC63i5dMcpBhFqmFy6R%26q-sign-time%3D1517809141%3B1517810941%26q-key-time%3D1517809141%3B1517810941%26q-header-list%3D%26q-url-param-list%3D%26q-signature%3Dcf55afb581b5c1f48661089bb2fe489ac77068c4&token=dfa52187ba15dcc9b9da8758a81af311875c448610001&clientIP=116.112.6.178&clientUA=963953f2-a85f-4edb-8081-143ace54e808">
    </swiper-slide>
    <swiper-slide>
      <img src="https://utaqi-1253553465.cos.ap-beijing.myqcloud.com/bg-3.jpg?sign=q-sign-algorithm%3Dsha1%26q-ak%3DAKIDZvVWbhg4UJzq1IsVMYyMGXwSJc1YPIeD%26q-sign-time%3D1517808085%3B1517809885%26q-key-time%3D1517808085%3B1517809885%26q-header-list%3D%26q-url-param-list%3D%26q-signature%3Ded53ba641143444d61878a168fae6ffc2ec7efbc&token=f331b56fdda53c282a2b5d1ee80b24300295912c10001&clientIP=116.112.6.178&clientUA=c505bae9-5694-4341-b413-c1abc973d088">
    </swiper-slide>
    <!-- Optional controls -->
    <div class="swiper-pagination"  slot="pagination"></div>
    <!-- <div class="swiper-scrollbar"   slot="scrollbar"></div> -->
  </swiper>
</template>

<script>
  require('swiper/dist/css/swiper.css') 
  export default {
    name: 'image-scroll',
    data() {
      return {
        swiperOption: {
          notNextTick: true,
          autoplay: 300,
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          // scrollbar:'.swiper-scrollbar',
          // mousewheelControl : true,
          onTransitionStart(swiper){
            console.log(swiper)
          }
        }
      }
    },
    // 如果你需要得到当前的swiper对象来做一些事情，你可以像下面这样定义一个方法属性来获取当前的swiper对象，同时notNextTick必须为true
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
      console.log('this is current swiper instance object', this.swiper)
      this.swiper.slideTo(3, 1000, false)
    }
  }
</script>

<style scoped>
  img{
    width: 100%;
    height: 100%;
  }
</style>


