<template>
	<div class="answer">
		<h3 class="title">蒙医助理医师模拟考试<strong style="color:#2278cf;">答案</strong></h3>
	    <div v-for="a in answers">
		    <div class="panel panel-default">
		        <!-- Default panel contents -->
		        <div class="panel-heading" style="padding:5px;"><p style="color:#2278cf;margin:5px;font-size:16px;">{{a.title}}</p></div>
		        <div class="bs-glyphicons">
			        <ul class="bs-glyphicons-list" style="padding:5px;">
			        	<li v-for="i in a.answers">
			        		<p>{{i.id}}</p>
			        		<p><strong>{{i.a}}</strong></p>
			        	</li>
			        </ul>
		        </div>
		    </div>
	  	</div>
	</div>
</template>

<script>
	export default{
		name:"answer",
		data(){
			return{
				answers:[]
			}
		},
		created(){
			this.$http.get('./../static/answers01.json')
		  		.then(function(data){
		  			console.log(data.json());
		        	return data.json(); //json解析
		  		})
		      .then(function(data){
		        this.answers = data;
		      })
		}
	}
</script>

<style>
	.answer{
	    padding-top: 20px;
	    padding-bottom: 20px;
	    margin: 20px;
	}
	ul{
		list-style-type: none;
		padding: 5px;
	}
	.bs-glyphicons li {
	    float: left;
	    width: 20%;
	    height: 80px;
	    padding: 10px;
	    font-size: 16px;
	    line-height: 1.4;
	    text-align: center;
	    background-color: #f9f9f9;
	    border: 1px solid #fff;
	}
	.bs-glyphicons {
	    margin-bottom: 5px;
	    overflow: hidden;
	}
	*{
		box-sizing: border-box;
	}
	h3{
		font-size: 18px;
		margin: 0 0 15px 0;
		padding: 0 0 0 10px;
	}
	@media (min-width: 768px) {
	  .bs-glyphicons {
	    margin-right: 0;
	    margin-left: 0;
	  }
	  .bs-glyphicons li {
	    width: 10%;
	    font-size: 16px;
	  }
	  h3{
	  	font-size: 24px;
	  }
	}
</style>


